.header {
    color: darkblue;
    text-decoration: underline;
}

.main {
    width: 40%;
    margin-bottom: 20px;
    border-radius: 10px;
    border: solid black;
    padding: 10px;
}

.center {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sub {
    width: 40%;
    margin-bottom: 10px;
    border-radius: 40px;
    border: solid green;
    padding: 10px;
    background-color: lightgoldenrodyellow;
    transition: background-color 1s;
}